import {useEffect, useState} from "react";
import Web3 from "web3";
import coineus_abi from "../abis/coineus.json";
import coineusone_abi from "../abis/coineusone_abi.json";
import {makeBatchRequest} from "../utils/promisify";
import {Container, Divider, Grid, ListItem, ListItemText, Paper, Typography} from "@mui/material";
import BurnSupply from "../components/BurntSupply";
import {useGetTokenQuery} from "../redux/services/dexscreener";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import axios from "axios";

import snapshot_cns from "../snapshot/cns.json";
import snapshot_cnsone from "../snapshot/cnsone.json";
import snapshot_cnsone2 from "../snapshot/cnsone2.json";
import {useSelector} from "react-redux";

const CoineusContractAddress = '0xc2E299b47398963c618De5b05C6BdeCd4cc64022';
const COINEUSONE_ADDRESS = '0x7042807d547A2de0720421a6d2f629c9EB43E73D';
const CNS_STORAGE_ADDRESS = '0x15C5C2CDb2676541a2f38919B7A82656a6C71F4F';


const cns_holders = {};
snapshot_cns.result.map( account => {
    cns_holders[account.address] = account.value;
})

const cnsone_holders = {};
snapshot_cnsone.result.map( account => {
    cnsone_holders[account.address] = account.value;
})

const cnsone2_holders = {};
snapshot_cnsone2.result.map( account => {
    cnsone2_holders[account.address] = account.value;

    console.log(account.address, cnsone_holders[account.address], account.value, (parseInt(account.value) - parseInt(cnsone_holders[account.address])) / (10**18) );
})

export default function Stats() {

    const {wallet} = useSelector(state => state.coineus);


    const [totalD, setTotalD] = useState(0);

    const [totalStaked, setTotalStaked] = useState(0);
    const [totalBurnt, setTotalBurnt] = useState(0);
    const [fundBalance, setFundBalance] = useState(0);
    const [holders, setHolders] = useState(0);

    const [cnsvoltAmount, setCnsvoltAmount] = useState(0);
    const [cnsfuseAmount, setCnsfuseAmount] = useState(0);

    const {data} = useGetTokenQuery(CoineusContractAddress, {
        pollingInterval: 60000
    });

    useEffect(() => {
        axios
            .get('https://explorer.fuse.io/api?module=token&action=getTokenHolders&contractaddress=0xc2E299b47398963c618De5b05C6BdeCd4cc64022&offset=10000')
            .then(resp => {
                setHolders(resp.data.result.length);
            })
    }, [])


    //updates coineus info
    useEffect(() => {
        getCoineusInfo();
        const interval = setInterval(getCoineusInfo, 60000)
        return () => clearInterval(interval);
    }, [])

    const getCoineusInfo = () => {
        const web3 = new Web3('https://rpc.fuse.io');
        const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);
        const cnsone_contract = new web3.eth.Contract(coineusone_abi, COINEUSONE_ADDRESS);
        const cnsvolt_contract = new web3.eth.Contract(coineus_abi, '0x836c205C49D9Db143f0A79DD384d08895f171C5f');
        const cnsfuse_contract = new web3.eth.Contract(coineus_abi, '0xeFA4222Fd80328558801Fe78ba3392048432d891');

        const calls = [
            contract.methods.getTotalDividendsDistributed().call,
            cnsone_contract.methods.totalSupply().call,
            cnsone_contract.methods.balanceOf('0x000000000000000000000000000000000000dEaD').call,
            contract.methods.balanceOf(CNS_STORAGE_ADDRESS).call,

            cnsvolt_contract.methods.totalSupply().call,
            cnsfuse_contract.methods.totalSupply().call,

        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                td,
                staked,
                burnt,
                fund_cns,

                cnsvolt_amount,
                cnsfuse_amount,

            ] = resp;

            setTotalD(td / (10 ** 18));

            setTotalStaked(staked / (10 ** 18))
            setTotalBurnt(burnt / (10 ** 18))

            setFundBalance(fund_cns / (10**18));

            setCnsvoltAmount(cnsvolt_amount / (10**18));
            setCnsfuseAmount(cnsfuse_amount / (10**18));
        });

    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                {
                    wallet.address && <Grid item xs={12}>
                        <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                            <Typography variant="h5" style={{padding:10, fontWeight: 700}}>Migration Info</Typography>
                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(cns_holders[wallet.address.toLowerCase()] / (10**18))} CNS` || '--'}
                                    secondary="CNS Held"
                                    primaryTypographyProps={{fontWeight: 700}}
                                    secondaryTypographyProps={{color: '#000'}}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(cnsone_holders[wallet.address.toLowerCase()] / (10**18))} CNSONE` || '--'}
                                    secondary="CNSONE Held"
                                    primaryTypographyProps={{fontWeight: 700}}
                                    secondaryTypographyProps={{color: '#000'}}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={`${CoineusCryptoFormat((parseInt(cnsone_holders[wallet.address.toLowerCase()]) + parseInt(cns_holders[wallet.address.toLowerCase()])) / (10**18))} CEUS` || '--'}
                                    secondary="CEUS to recieve"
                                    primaryTypographyProps={{fontWeight: 700}}
                                    secondaryTypographyProps={{color: '#000'}}
                                />
                            </ListItem>
                        </Paper>
                    </Grid>
                }

                {
                    data && data.pairs && data.pairs[0] && <>
                        <Grid item xs={12}>
                            <BurnSupply
                                supply={2000000}
                                burnt={totalBurnt}
                                percent={totalBurnt / 2000000}
                                faucet={0}
                                fund={fundBalance}
                                liquidity={data?.pairs[0]?.liquidity.base}

                                cnsone={totalStaked}
                                cnsvolt={cnsvoltAmount}
                                cnsbnb={0}
                                cnsfuse={cnsfuseAmount}
                            />
                        </Grid>

                        <Grid item xs={12}>
                            <Divider style={{marginBottom:10}}>Coineus</Divider>
                            <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                                <ListItem>
                                    <ListItemText
                                        primary={`${CoineusCryptoFormat(totalD)} fUSD` || '--'}
                                        secondary="Total Rewards All-time"
                                        primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                                        secondaryTypographyProps={{color: '#000'}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>

                        <Grid item xs={12}>
                            <Paper>
                                <ListItem>
                                    <ListItemText
                                        primary={holders}
                                        secondary="Coineus Holders"
                                        primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>

                        <Grid item xs={12}>
                            <Paper>
                                <ListItem>
                                    <ListItemText
                                        primary={CoineusUSDFormat(data?.pairs[0]?.fdv)}
                                        secondary="Fully Diluted Value"
                                        primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>

                        <Grid item xs={12}>
                            <Paper>
                                <ListItem>
                                    <ListItemText
                                        primary={CoineusUSDFormat(data?.pairs[0]?.liquidity.usd)}
                                        secondary="Total Liquidity"
                                        primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                                    />
                                </ListItem>
                                <ListItem>
                                    <ListItemText
                                        primary={CoineusCryptoFormat(data?.pairs[0]?.liquidity.base)}
                                        secondary="Coineus (CNS)"
                                        primaryTypographyProps={{ fontWeight: 700}}
                                    />
                                    <ListItemText
                                        primary={CoineusCryptoFormat(data?.pairs[0]?.liquidity.quote)}
                                        secondary="Fuse (WFUSE)"
                                        primaryTypographyProps={{fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>

                        <Grid item xs={12}>
                            <Paper>
                                <ListItem>
                                    <ListItemText
                                        primary={CoineusUSDFormat(data?.pairs[0]?.volume.h24)}
                                        secondary="24 Hour Volume"
                                        primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem'}}
                                    />
                                </ListItem>
                                <ListItem>
                                    <ListItemText
                                        primary={data?.pairs[0]?.txns.h24.buys}
                                        secondary="Buys"
                                        primaryTypographyProps={{ fontWeight: 700}}
                                    />
                                    <ListItemText
                                        primary={data?.pairs[0]?.txns.h24.sells}
                                        secondary="Sells"
                                        primaryTypographyProps={{fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                    </>
                }


            </Grid>
        </Container>
    )

}