import {Container, Grid, Table, TableCell, TableRow, Typography} from "@mui/material";

import snapshot_cns from "../snapshot/cns.json";
import snapshot_cnsone from "../snapshot/cnsone.json";
import {useSelector} from "react-redux";

const ceus_holders = {};

const cns_holders = {};
snapshot_cns.result.map( account => {
    cns_holders[account.address] = (account.value);
    ceus_holders[account.address] = (account.value);
})

const cnsone_holders = {};
snapshot_cnsone.result.map( account => {
    if(!ceus_holders[account.address]){
        ceus_holders[account.address] = 0;
    }
    cnsone_holders[account.address] = (account.value);

    ceus_holders[account.address] = parseInt(ceus_holders[account.address]) + (parseInt(account.value));
})

console.log(ceus_holders);

export default function Migration() {

    const {wallet} = useSelector(state => state.coineus);


    return(<Container>
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Table>
                { Object.entries(ceus_holders).map( account => {
                    return <TableRow>
                        <TableCell>{account[0]}</TableCell>
                        <TableCell>{cns_holders[account[0]] / (10**18) || '--'}</TableCell>
                        <TableCell>{cnsone_holders[account[0]] / (10**18) || '--'}</TableCell>
                        <TableCell>{account[1] / (10**18)}</TableCell>
                    </TableRow>
                })}
            </Table>
        </Grid>
    </Container>
    )
}